:root {
  --color-primary: #007bff;
  --color-primary-hover: #0056b3;
  --color-primary-muted: #e6f2ff;
  --color-background: #f8f9fa;
  --color-surface: #fff;
  --color-text: #343a40;
  --color-text-muted: #6c757d;
  --color-headings: #212529;
  --color-border: #dee2e6;
  --color-danger: #dc3545;
  --color-danger-hover: #c82333;
  --color-container-shadow: #0000000d;
  --spacing-xs: .25rem;
  --spacing-sm: .5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
  --border-radius-sm: 4px;
  --border-radius-md: 8px;
  --border-radius-lg: 50px;
}

body {
  background-color: var(--color-background);
  color: var(--color-text);
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  line-height: 1.6;
  display: flex;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--color-headings);
  margin-top: 0;
  margin-bottom: var(--spacing-md);
  line-height: 1.2;
}

a {
  color: var(--color-primary);
  text-decoration: none;
  transition: color .2s ease-in-out;
}

a:hover {
  color: var(--color-primary-hover);
  text-decoration: underline;
}

p {
  margin-top: 0;
  margin-bottom: var(--spacing-md);
}

.container {
  width: 100%;
  max-width: 800px;
  padding: var(--spacing-lg) var(--spacing-md);
  box-sizing: border-box;
  margin: 0 auto;
}

.app-header {
  background-color: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  padding: var(--spacing-md) var(--spacing-lg);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.app-header .logo {
  color: var(--color-headings);
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
}

.app-header .main-nav {
  align-items: center;
  gap: var(--spacing-md);
  display: flex;
}

.app-header .main-nav a:not(.btn) {
  color: var(--color-text-muted);
  font-weight: 500;
}

.app-header .main-nav a:hover:not(.btn) {
  color: var(--color-primary);
  text-decoration: none;
}

.app-footer {
  background-color: var(--color-surface);
  border-top: 1px solid var(--color-border);
  padding: var(--spacing-lg);
  text-align: center;
  color: var(--color-text-muted);
  margin-top: auto;
}

.app-footer .footer-nav {
  margin-top: var(--spacing-sm);
}

.app-footer .footer-nav a {
  margin: 0 var(--spacing-sm);
  color: var(--color-text-muted);
}

.btn {
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius-sm);
  cursor: pointer;
  color: #fff;
  border: none;
  font-size: .9rem;
  font-weight: bold;
  text-decoration: none;
  transition: background-color .2s ease-in-out, transform .1s;
  display: inline-block;
}

.btn:hover {
  text-decoration: none;
  transform: translateY(-1px);
}

.btn-primary {
  background-color: var(--color-primary);
}

.btn-primary:hover {
  background-color: var(--color-primary-hover);
  color: #fff;
}

.form-group {
  margin-bottom: var(--spacing-md);
}

label, legend {
  margin-bottom: var(--spacing-sm);
  font-weight: bold;
  display: block;
}

input[type="text"], input[type="email"], input[type="password"], textarea {
  width: 100%;
  padding: var(--spacing-sm);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-sm);
  box-sizing: border-box;
  background-color: var(--color-surface);
  color: var(--color-text);
  font-size: 1rem;
}

textarea {
  resize: vertical;
  min-height: 150px;
}

.error-message {
  color: var(--color-danger);
  margin-top: var(--spacing-sm);
  font-weight: bold;
}

.checkbox-group {
  gap: var(--spacing-md);
  flex-wrap: wrap;
  display: flex;
}

.checkbox-item {
  align-items: center;
  display: flex;
}

.checkbox-item input[type="checkbox"] {
  width: auto;
  margin-right: var(--spacing-sm);
}

.post-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.post-card {
  background: var(--color-surface);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  border-radius: var(--border-radius-md);
  box-shadow: 0 4px 12px var(--color-container-shadow);
  transition: box-shadow .2s ease-in-out;
}

.post-card:hover {
  box-shadow: 0 6px 16px var(--color-container-shadow);
}

.post-card-header h2 {
  margin: 0;
  font-size: 1.5rem;
}

.post-card-header h2 a {
  color: var(--color-headings);
  text-decoration: none;
}

.post-card-meta {
  color: var(--color-text-muted);
  margin-bottom: var(--spacing-md);
  font-size: .9rem;
}

.post-card-content {
  margin-bottom: var(--spacing-md);
}

.post-card-footer {
  justify-content: space-between;
  align-items: center;
  gap: var(--spacing-md);
  display: flex;
}

.post-full {
  background: var(--color-surface);
  padding: var(--spacing-xl);
  border-radius: var(--border-radius-md);
}

.post-full .post-header h1 {
  font-size: 2.5rem;
}

.post-full .post-meta {
  color: var(--color-text-muted);
  margin-bottom: var(--spacing-xl);
}

.post-full .post-body {
  margin-bottom: var(--spacing-xl);
  font-size: 1.1rem;
  line-height: 1.7;
}

.category-filter {
  margin-bottom: var(--spacing-lg);
  padding-bottom: var(--spacing-lg);
  border-bottom: 1px solid var(--color-border);
}

.category-tags-container {
  gap: var(--spacing-sm);
  flex-wrap: wrap;
  display: flex;
}

.category-tag {
  background-color: var(--color-primary-muted);
  color: var(--color-primary);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius-lg);
  margin-right: var(--spacing-xs);
  font-size: .8rem;
  font-weight: 500;
  transition: background-color .2s, color .2s;
  display: inline-block;
}

.category-filter .category-tag {
  cursor: pointer;
}

.category-filter .category-tag:hover, .category-filter .category-tag.active {
  background-color: var(--color-primary);
  color: #fff;
}

.user-menu {
  align-items: center;
  gap: var(--spacing-md);
  display: flex;
}

.user-menu .username {
  color: var(--color-headings);
  font-weight: bold;
}

.main-controls {
  margin-bottom: var(--spacing-lg);
  justify-content: space-between;
  align-items: center;
  display: flex;
}
